<script>
import Hashids from 'hashids';
import store from '@/store';
import { modalClose, modalOpen } from '@/actions/modal';

const initialState = {
    companyName: '',
    sameAddress: false,
    noFax: true
};
const hashidsGen = new Hashids();


export default {
    name: 'map',
    data() {
        return {
            form: { ...initialState },
            modal: this.$select('modal') || {},
            quickNavList: [
                { name: ' ', id: 0, status: '0', current: false },
                { name: '', id: 1, status: '0', current: false },
                { name: ' ', id: 2, status: '0', current: false },
                { name: '', id: 3, status: '0', current: false },
                { name: ' ', id: 4, status: '0', current: false },
                { name: ' ', id: 5, status: '0', current: false },
                { name: '/ ,    ', id: 6, status: '0', current: false }
            ]
        };
    },
    props: {
        formId: {
            //   
            type: String,
            'default': ''
        },
        requestId: {
            type: String,
            'default': ''
        }
    },
    methods: {
        getHash(key) {
            return hashidsGen.encode(key);
        },
        handleModalClose(modalId) {
            store.dispatch(modalClose(modalId));
        },
        openAddCommonPerson(event) {
            event.preventDefault();
            store.dispatch(modalOpen('newCommonPerson'));
        },
        openAddLegalPerson(event) {
            event.preventDefault();
            store.dispatch(modalOpen('newLegalPerson'));
        },
        openAddItem(event) {
            event.preventDefault();
            store.dispatch(modalOpen('newItem'));
        },
        validateForm() {
            // eslint-disable-next-line
            console.log(this.form.sameAddress);
            this.form = { ...this.form, ...initialState };
        }
    }
};
</script>




